<script>
import $ from 'jquery'
//  Pagination
import Pagination from '../components/Pagination'
import Hamburger from '@/components/Hamburger'
import cityName from '../assets/CityCountyData.json'

export default {
  //  Pagination
  components: {
    Pagination,
    Hamburger
  },
  //   () => ({})  ()
  data: () => ({
    allData: [],
    data: [],
    filterData: [],
    cityFilterData: [],
    searchText: '',
    cityName,
    select: {
      city: '',
      area: '',
      category: ''
    },
    allPagination: {},
    pagination: {},
    tempLocation: {
      geometry: {
        type: 'Point',
        coordinates: {}
      }
    }, // modal 
    isNew: false,
    deleteId: '',
    deleteName: '',
    updateId: '',
    status: {
      fileUploading: false
    },
    isLoading: false
  }),
  methods: {
    getAllLocation() {
      const api = `${process.env.VUE_APP_APIPATH}/restaurants`
      this.isLoading = true
      this.$http.get(api).then(Response => {
        //   console.log(Response)
        this.allData = Response.data.data
        // console.log(this.data)
        this.isLoading = false
      })
    },
    getLocation(page = 1) {
      //  axios 
      const api = `${process.env.VUE_APP_APIPATH}/restaurants/page?page=${page}`
      this.$http.get(api).then(Response => {
        // console.log(Response.data)
        this.data = Response.data.data
        this.pagination = Response.data.pagination
        // console.log(this.data, this.pagination)
      })
    },
    openModal(isNew, item) {
      $('#locationModal').modal('show')
      //  geometry 
      if (isNew) {
        this.tempLocation = {
          geometry: {
            type: 'Point',
            coordinates: []
          }
        }
        this.isNew = true
      } else {
        //  this.tempLocation = item ES6  Object.assign()
        this.tempLocation = Object.assign({}, item)
        // console.log(this.tempLocation)
        this.updateId = this.tempLocation._id
        this.isNew = false
      }
      $('#locationModal').modal('show')
    },
    updateLocation() {
      // updata API
      let api = `${process.env.VUE_APP_APIPATH}/restaurants`
      let httpMethod = 'post'
      const vm = this
      if (!vm.isNew) {
        // 
        api = `${process.env.VUE_APP_APIPATH}/restaurants/${vm.updateId}`
        httpMethod = 'put'
        //  tempLocation._id id mongodb 
        delete this.tempLocation._id
        // console.log(api)
      }
      //  scorecost  parseFloat 
      vm.tempLocation.score = parseFloat(vm.tempLocation.score)
      vm.tempLocation.cost = parseInt(vm.tempLocation.cost)
      // console.log(vm.tempLocation)
      // vm.tempLocation 
      this.$http[httpMethod](api, vm.tempLocation).then(response => {
        // console.log(response.data)
        if (response.data.success) {
          //  modal
          $('#locationModal').modal('hide')
          vm.getLocation()
        } else {
          $('#locationModal').modal('hide')
          vm.getLocation()
        }
      })
    },
    delModal(item) {
      $('#delLocationModal').modal('show')
      //  item.id
      this.deleteId = item._id
      this.deleteName = item.name
      // console.log(item._id)
    },
    deleteLocation() {
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/restaurants/${vm.deleteId}`
      this.$http.delete(api).then(response => {
        // console.log(response.data)
        if (response.data.success) {
          $('#delLocationModal').modal('hide') //  modal
          console.log('')
          vm.getLocation()
        } else {
          $('#delLocationModal').modal('hide') //  modal
          vm.getLocation()
        }
      })
    },
    uploadFile() {
      // 
      // console.log(this);
      const uploadedFile = this.$refs.files.files[0]
      const vm = this
      const formData = new FormData() //  
      formData.append('file-to-upload', uploadedFile) // 
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`
      vm.status.fileUploading = true
      this.$http
        .post(url, formData, {
          headers: {
            'content-Type': 'multipart/form-data' //  form-data
          }
        })
        .then(response => {
          console.log(response.data)
          vm.status.fileUploading = false
          if (response.data.success) {
            // vm.tempProduct.imageUrl = response.data.imageUrl; // 
            // console.log(vm.tempProduct.imageUrl);
            vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl) // 
          } else {
            // this.$bus.$emit("message:push", response.data.message, "danger"); // 
          }
        })
    },
    signout() {
      const api = `${process.env.VUE_APP_APIPATH}/user/logout`
      const vm = this
      this.$http.get(api).then(response => {
        console.log(response.data)
        if (response.data.success) {
          vm.$router.push('/login') // 
        }
      })
    },
    citySearch(page = 1) {
      const api = `${process.env.VUE_APP_APIPATH}/restaurants/page?page=${page}&&city=${this.select.city}`
      this.$http.get(api).then(Response => {
        console.log(Response.data)
        this.data = Response.data.data
        this.pagination = Response.data.pagination
        // console.log(this.data, this.pagination)
      })
    },
    search() {
      if (this.searchText) {
        const filter = this.allData.filter(
          item => item.name === this.searchText
        )
        // console.log(filter)
        this.filterData = filter
      }
    }
  },
  mounted() {
    this.getAllLocation()
    this.getLocation()
  }
}
</script>

