<script>
// Line 
import { Line, mixins } from 'vue-chartjs'
// reactiveProp  chartData  prop  watch  prop  data 
const { reactiveProp } = mixins

export default {
  extends: Line,
  mixins: [reactiveProp],
  data: () => ({
    // props: {
    //   chartData: {
    //     type: Object,
    //     default: null
    //   }
    // },
    options: {
      // RWD
      responsive: true,
      maintainAspectRatio: false,
      // 
      scales: {
        // y
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
              stepSize: 1,
              fontSize: 20,
              fontColor: 'green'
            }
          }
        ]
      },
      legend: {
        // position: 'right',
        // align: 'start',
        labels: {
          fontColor: 'black',
          fontSize: 16,
          fontFamily: 'Microsoft YaHei',
          padding: 30
        }
      },
      // 
      animation: {
        // 
        duration: 1000
        // 
        // onComplete: function() {
        //   alert('hi')
        // }
      }
    }
  }),
  // 
  mounted() {
    this.chartRender()
  },
  methods: {
    chartRender() {
      this.renderChart(this.chartData, this.options)
    },
    //  labels
    labelsUpdate() {
      this.updateLabels(this.chartData.labels)
    }
  }
}
</script>