<template>
  <ul class="star-box">
    <li v-for="(i, index) in 5" :key="i">
      <span :style="{width: starWidth(index)}"></span>
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    score: {
      type: Number,
      default: 0
    }
  },
  data: () => ({
    starArr: []
  }),
  methods: {
    initStar() {
      // | 
      const int = this.score | 0
      // 
      const point = this.score.toFixed(1).split('.')[1]
      for (let s = 0; s < int; s++) {
        this.starArr.push(1)
      }
      // console.log(this.starArr)
      if (point === '0') return
      // + 
      this.starArr.push(+`0.${point}`)
    },
    starWidth(index) {
      //  NaN
      const w = (this.starArr[index] || 0) * 100
      return `${w}%`
    }
  },
  mounted() {
    this.initStar()
  }
}
</script>


<style lang="scss" scoped>
.star-box {
  display: flex;
  justify-content: space-between;
  width: 180px;
  // margin: 0 auto;
  padding: 0;
  // star
  li {
    display: block;
    width: 30px;
    height: 30px;
    background-image: url('../assets/images/estar.svg');
    background-size: auto 100%;
    margin: 0 3px;
    // yellow star
    span {
      display: block;
      width: 0%;
      height: 100%;
      background-image: url('../assets/images/star.svg');
      background-size: auto 100%;
    }
  }
}
</style>